*{
  margin: 0;
  padding: 0;

}
body{
  background-color:#fdf5e6;
  display: flex;
  justify-content:center;
  align-items: center;
  height: 100vh;
}
.header
{
font-size: 30px;
margin-left: 50%;
color: #1212ec;
}
.contents
{
  background: linear-gradient(97deg,#9f4599 0%, rgba(0,255,0,0.92) 80%);
  min-height: 300px;
  height: min-content;
  width: 95%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border-radius: 20px;
  box-shadow: 10px 10px 40px 0px rgba(0,0,0,0.75);
}
.score-section
{
  display: flex;
  align-items: center;
  color:white;
  font-weight: bold;
}
.question-section{
  position: relative;
  margin-left: 20px;
  width: 100%;
}
.question-count
{
  margin-bottom: 20px;
font-size: 20px;
}
.question-count span{
  font-size: 30px;
}
.question.text
{
  position: absolute;
  color: white;
  font-size: 29px;
  font-weight: bold;
}
.answer-section
{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-around;
  padding: 10px;
  height: 100%;
}
button{
  width: 100%;
  font-size: 15px;
  padding: 5px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
}